@import "common/mixins";
@import "common/variables";

.blockimg {
  position: relative;
  text-align: center;

  &__body {
  }

  &__item {
    position: relative;
    z-index: 2;
  }
}

// ---------------------------------------------------------
// ++++++++++++++++++++++ blockkk  +++++++++++++++++++++++++++++++++++
// ---------------------------------------------------------
.blockkk {
  background-color: #bb8830;
  outline: 2px solid red;

  &__body {
    position: relative;
    width: 100%;
    height: 100%;
    margin: 0px auto;
    background-color: #5c4317;
    text-align: center;
    @include flex();
    flex-wrap: wrap;
  }
  &__header {
    flex: 0 1 100%;
  }
  &__plaza {
    flex: 0 1 100%;
  }
  &__button {
    flex: 0 1 30%;
  }
}

// ------ шапка--------------------
.header-blockkk {
  &__body {
  }
  &__icon {
    background-color: #bee1ff;
  }

  &__title {
    background-color: #3dbbff;
  }

  &__text {
    background-color: #246eb9;
  }
}

// -----контейнер для единичны блоков-----------------
.plaza-blockkk {
  &__body {
    background-color: #ff9419;
    @include flex();
    width: 100%;
    height: 100%;
  }
  &__item {
    outline: 2px solid aqua;
    flex: 0 1 100%;
    @media screen and (max-width: 600px) {
      flex: 0 1 100%;
    }
    &_duble {
      flex: 0 1 100%;
      @media screen and (max-width: 600px) {
        flex: 0 1 100%;
      }
    }
  }
}
// ---------- единичный блок---------------------------
.item-plaza-blockkk {
  background-color: #193549;
  &__body {
    @include jucc();
    flex-wrap: wrap;
  }
  &__item {
    flex: 0 1 100%;

    &_icon {
      background-color: #bee1ff;
    }
    &_title {
      background-color: #9e9e9e;

      flex: 0 1 100%;
      font-family: Lobster-Regular;
      font-size: 15px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.67;
      letter-spacing: normal;
      text-align: left;
    }
    &_text {
      background-color: #b29c69;
      font-family: Lobster-Regular;
      font-size: 15px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.67;
      letter-spacing: normal;
      text-align: left;
    }
    &_button {
    }
  }
}
// ----------двойной единичный блок ---------------------------
.item-plaza-blockkk-duble {
  &__body {
    width: 100%;
    height: 100%;
    @include jucc();
  }
  &__left {
    flex: 0 1 80%;
    @media screen and (max-width: 600px) {
      flex: 0 1 100%;
    }
  }
  &__right {
    flex: 0 1 20%;
  }
}

.right-blockkk-duble {
  outline: 2px solid #a6afff;
  &__body {
    width: 100%;
    height: 100%;
    @include jucc();
  }
  &__head {
    background-color: #6e6e6e;
    flex: 0 1 100%;

    font-family: Lobster-Regular;
    font-size: 15px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.67;
    letter-spacing: normal;
    text-align: left;
  }
  &__foot {
    background-color: #856f3c;
    flex: 0 1 100%;

    font-family: Lobster-Regular;
    font-size: 15px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.67;
    letter-spacing: normal;
    text-align: left;
  }
}

// ---------------------------------------------
.button-item-plaza-blockkk,
.button-left-item-plaza-blockkk-duble {
  outline: 2px solid #4d2342;
  background-color: #3ad900;
  @include anim(0.2s);
  padding: 5px 0px 5px 0px;
  &:hover {
  }
  span {
    padding: 50px 10px;
    font-family: Lobster-Regular;
    font-size: 15px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.67;
    letter-spacing: normal;
    text-align: left;
  }
}

.button-blockkk {
  outline: 2px solid #4d2342;
  background-color: #dc4c2f;
  @include anim(0.2s);
  margin: 0px auto;
  span {
    width: 100%;
    height: 100%;
    background-color: #9fff0f;
    padding: 20px 10px;
    font-family: Lobster-Regular;
    font-size: 15px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.67;
    letter-spacing: normal;
    @include anim(0.2s);
  }
  &:hover {
    span {
      background-color: #64c29e;
    }
  }
}

